
html, body, #app {
  margin: 0;
  width: 100vw;
  height: 100vh;
  overflow: hidden;
}
#app {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
  -ms-flex-align: center;
      align-items: center;
  -ms-flex-pack: center;
      justify-content: center;
}
#video {
  width: 100%;
  height: 100%;
  margin: 0;
  transform: scale(1.2);
  position: fixed;
  z-index: -1;
}
@media all and (max-width: 600px) {
#app {
    background-image: url('http://assets.worldwildlife.org/photos/340/images/hero_full/HI_257682.jpg?1434400464');
}
}
.words {
  margin-top: 10%;
}
.word {
  padding: 5px;
  font-weight: bold;
  color: white;
  text-align: center;
  font-size: 3em;
  font-family: Arial,"Helvetica Neue",Helvetica,sans-serif
}
.fade-enter-active, .fade-leave-active {
  transition: opacity .5s;
}
.fade-enter, .fade-leave-to {
  opacity: 0;
}
.fade-enter-to {
  opacity: 1;
}
.fade-move {
  transition: transform 0.5s;
}



